function eq(a, b) {
  //  `===`  true  0  -0
  if (a === b) return a !== 0 || 1 / a === 1 / b;

  //  undefined  null 
  if (a == null || b == null) return false;

  //  NaN
  if (a !== a) return b !== b;

  //  a b  false
  if (typeof a !== 'object' && typeof a !== 'function' && typeof b !== 'object' /* && typeof b !== 'function'*/) return false

  return deepEq(a, b)
}

function deepEq(a, b, aStack, bStack) {
  //  false
  var className = Object.prototype.toString(a)
  if (className !== Object.prototype.toString(b)) return false

  // , 
  switch(className) {
    case '[object Number]':
    case ['object Boolean']:
    case ['object Date']:
      return eq(+a, +b);
    case '[object String]':
    case '[object RegExp]':
    case '[object Function]':
      return eq('' + a, '' + b);
  }

  // 
  aStack = aStack || [];
  bStack = bStack || [];
  var index = bStack.indexOf(b)
  if (index !== -1) {
    return aStack[index] === a;
  }

  var length;
  // 
  if (className === '[object Array]') {
    length = a.length;
    if (length !== b.length) return false;
    while (length--) {
      if (!eq(a[length], b[length], aStack, bStack)) return false;
    }
  } else {
    // 
    //  false
    var aCtor = a.constructor
    var bCtor = b.constructor
    if (aCtor !== bCtor &&
      typeof aCtor === 'function' && a instanceof aCtor &&
      typeof bCtor === 'function' && b instanceof bCtor
    ) {
      return false
    }
    // 
    var keys, key;
    keys = Object.keys(a);
    length = keys.length;
    if (length !== Object.keys(b).length) return false;
    while (length--) {
      key = keys[length];
      if (!eq(a[key], b[key], aStack, bStack)) return false;
    }
  }

  aStack.pop();
  bStack.pop();

  return true;
}

export default eq