import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations'
import actions from './actions'
import { initState, wrapPersistence } from './persistence'

Vue.use(Vuex);

 const store = new Vuex.Store({
  state: initState({
    userId: '', // id
    user: null, // 

    cartMap: {}, // 
    currentRestaurantId: '', //  id

    addressList: null, // ?Array, 
    selectedAddressId: '', // 
    editingAddress: '', // 

    remarkList: null, // ?Array, 
    usedRemark: null, // ?Object, 

    /* location */
    latitude: null,    // ?Number
    longitude: null,  // ?Number
    geohash: '',
    locationName: '', // Index 
    cityName: '',   // SelectCity 
    cityList: null, // 



  }),
  getters: {
    selectedAddress(state) {
      const id = state.selectedAddressId;
      return (state.addressList || []).find(addr => addr.id === id)
    },
    remarkText(state) {
      if (!state.usedRemark) return ''

      const { inputed, selected = [] } = state.usedRemark
      return selected
        .map(v => v && v.join(''))
        .concat(inputed)
        .filter(v => v)
        .join(',')
    },
  },
  mutations,
  actions,
});

wrapPersistence(store)

export default store
